@import "_import";

$notosanskrm: 'Noto Sans KR Medium';
$notosanskr: 'Noto Sans KR';
$rosendale: 'roslindaletext';
$rosendaleb: 'roslindaletext-bold';
$rosendalei: 'roslindaletext-italic';
$ambit-semi: 'ambit-semi';
$ambit-regular: 'ambit-regular';
$tiempos: 'tiempos';
$athletics: 'athletics';
$ios-gap: 52px;

$connect-top: 0;

$connect-z-index: 40;
$forest-z-index: 50;
$art-z-index: 60;
$safety-z-index: 70;
$exp-z-index: 80;

.ios,
.android {
	@include mobile {
		#frame {
			.letters-second {
				.letters__inner {
					top: $ios-gap;
				}
			}
			.frame__inner {
				top: -#{$ios-gap};
			}
			.kv__masked__keyvisual {
				top: #{$ios-gap};
			}
			.letters--expanded {
				position: absolute;
			}
		}
		#connect {
			.section-title {
				bottom: 120rem;
			}
		}
	}
}

.android {
	@include mobile {
		#frame {
			.letters-second {
				.letters__inner {
					top: 0;
				}
			}
		}
		#connect {
			.section-title {
				bottom: 80rem;
			}
		}
	}
}

// PC 섹션 공통 스타일
@mixin section-set-pc {
	position: relative;
	overflow: hidden;
	padding: 92px 60rem 0;
	@include full {
		padding: 92px 60px 0;
	}
}

// MO 섹션 공통 스타일
$side-padding: 15rem;
$mo-forest-pt: 38rem;
@mixin section-set-mo {
	position: relative;
	margin-top: 0;
	padding: 70px $side-padding 0;
	overflow: hidden;
}

// 텍스트 영역 믹스인
@mixin text-area-pc {
	.text-area {
		position: absolute;
		left: -100vw;
		bottom: -24rem;
		.catchphares {
			margin-top: 42rem;
			margin-left: -10rem;
			width: 200%;
			z-index: 1;
		}
	}
}

// kv 메인 텍스트 인터렉션
.motion-txt {
	&.motion-txt--start {
		.letter {
			overflow: hidden;
			&:nth-child(1) {
				.txt-target {
					transition-delay: 0.1s;
				}
			}
			&:nth-child(3) {
				.txt-target {
					transition-delay: 0.2s;
				}
			}
			&:nth-child(5) {
				.txt-target {
					transition-delay: 0.3s;
				}
			}
		}
		.note {
			overflow: hidden;
			&--01 {
				.txt-target {
					transition-delay: 0.5s;
				}
			}
			&--02 {
				.txt-target {
					transition-delay: 0.7s;
				}
			}
		}
	}
}

// 공통 인터렉션 (이미지)
.img-box {
	@include img-effect;
}

// 공통 인터렉션 (텍스트)
.motion-txt {
	@include text-effect('.txt-target');
}


// 페이지 공통 이너
.section__inner {
	position: relative;
	max-width: 1920px;
	margin: 0 auto;
}

/// 공통 캐치프레이즈
.catchphares {
	position: relative;
	display: block;

	&__title {
		display: flex;
		font-size: 240rem;
		justify-content: flex-start;
		align-items: baseline;
		@include ie-only {
			line-height: 1.1;
		}
		@include mobile {
			flex-wrap: wrap;
			font-size: 80rem;
		}
		span {
			display: inline-block;
			vertical-align: top;
			font-family: $ambit-semi;
			text-transform: uppercase;
		}
		em {
			font-family: $rosendale;
			font-size: 230rem;
			@include mobile {
				font-size: 74rem;
			}
		}
	}
}

// 페이지 공통 타이틀, 키워드 고정 스타일
.section-title-wrap {
	// background: rgba(#cd83fc, .4);
	position: absolute;
	top: 0;
	left: 0;
	z-index: 5;
	width: 100%;
	opacity: 1;
	transition: opacity 0.3s ease-in;
	@include mobile {
		position: static;
	}
}

// 공통 타이틀
.section-title {
	@include mobile {
		transform: none;
	}
	.title {
		font-size: 62rem;
		line-height: 1;
		text-transform: uppercase;
		font-family: $ambit-semi;
		font-weight: 500;
		@include full {
			font-size: 62px;
		}
		@include mobile {
			margin-top: 0;
			font-size: 30rem;
		}
		em {
			text-transform: none;
			letter-spacing: -1rem;
			font-family: $rosendale;
		}
	}
	&__link {
		overflow: visible;
	}
	a {
		display: inline-block;
		margin-top: 30rem;
		padding: 9rem 14rem;
		border: 3px solid #000;
		border-radius: 30rem;
		overflow: visible;
		font-size: 26rem;
		font-family: $ambit-semi;
		box-sizing: border-box;
		@at-root .br-win-ch &{
			padding-top: 15rem;
		}
		@at-root .br-ie &{
			padding-top: 16rem;
		}
		@include full {
			margin-top: 30px;
			padding: 9px 14px;
			border-radius: 30px;
			font-size: 26px;
		}
		@include mobile {
			margin-top: 12rem;
			padding: 7rem 10rem;
			font-size: 16rem;
			border-width: 2px;
			@at-root .br-win-ch &{
				padding-top: 10rem;
			}
		}
	}
}

// 페이지 공통 디스크립션
.info-text {
	margin-top: 0;
	padding-top: 20rem;
	font-size: 36rem;
	line-height: 1.38;
	font-family: $notosanskrm;
	word-break: break-all;
	&--small {
		font-size: 20rem;
	}
	@include full {
		padding-top: 20px;
		font-size: 36px;
	}
	@include mobile{
		font-size: 20rem;
		line-height: 1.5;
	}
}


// 메인 body
.p-main {
	position: relative;
	transition: all .5s ease-in;
	&.loading {
		height: 100vh;
		overflow: hidden;
	}
	&.loaded {
		#frame {
			.letter-cover {
				display: none;
			}
			.kv__masking {
				transform: scale(6,6);
			}
			.kv__masked__keyvisual--scale {
				opacity: 1;
			}

		}
	}
	.l-frame__content {
		overflow: hidden;
		@include mobile {
			padding-bottom: 500px; // mobile title 하단 강제 여백
			margin-bottom: -500px;
		}
	}
}



// Frame 스타일
#frame {
	position: relative;
	overflow: hidden;
	z-index: 1;
	width: 100vw;
	height: 200vh;
	@include mobile {
		min-height: 600rem;
	}

	.frame__inner {
		position: relative;
	}

	// kv 레터 스타일
	.letters {
		position: absolute;
		top: 50%;
		left: 50%;
		z-index: 3;
		width: 460rem;
		height: 680rem;
		max-width: 1920px;
		transform: translate(-50%, -50%);
		color: #fff;
		text-align: center;

		> [class^=letters] {
			height: 100%;
		}
		.letters-first{
			display: block;
		}
		.letters-second{
			display: none;
		}
		@include full {
			width: 460px;
			height: 680px;
		}

		@include pc {
			height: 600rem;
		}

		@include mobile {
			width: 230rem;
			height: 340rem;
			min-width: inherit;
			min-height: inherit;
		}

		&__inner {
			position: relative;
			width: 100%;
			height: 100%;
			text-transform: uppercase;

			&.motion-txt {
				.txt-target {
					opacity: 0;
				}
				&.txt-hello {
					.txt-target {
						opacity: 1;
					}
				}
			}

			@include tablet {
				width: 240rem;
			}

			// 키 워드 스타일
			.letter {
				position: absolute;
				left: 50%;
				display: block;
				transform: translate(-50%, 0);

				font-size: 90rem;
				font-family: $ambit-semi;
				white-space: nowrap;
				em {
					font-family: $rosendalei;
					font-size: 82rem;
					@include full {
						font-size: 82px;
					}
					@include mobile {
						font-size: 44rem;
					}
				}
				@include ie-only {
					line-height: 1.1;
				}
				@include mobile {
					font-size: 48rem;
				}
				&--01 {
					top: 22rem;
				}
				&--02 {
					top: 50%;
					transform: translate(-50%, -50%);
				}
				&--03 {
					bottom: 22rem;
					@include mobile {
						font-size: 40rem;
					}
				}
			}

			//보조 워드 스타일
			.note {
				position: absolute;
				display: block;
				left: 50%;
				font-size: 18rem;
				font-family: $notosanskr;
				line-height: 1.2;
				transform: translate(-50%, 0);
				@include ie-only {
					line-height: 1.1;
				}
				@include mobile {
					font-size: 12rem;
				}
				&--01 {
					top: 188rem;
					@include pc {
						top: calc(50% - 140rem);
					}
					@include mobile {
						top: 90rem;
					}
				}
				&--02 {
					top: 460rem;
					@include pc {
						top: calc(50% + 120rem);
					}
					@include mobile {
						top: auto;
						bottom: calc(90rem - 2rem);
					}
				}
			}
		}

		// kv 레터 Scene2 확장 스타일
		&--expanded {
			position: fixed;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 100vw;
			@include mobile {
				height: 100vh;
			}
			.letters-first{
				display: none;
			}
			.letters-second{
				display: block;
			}
			.letters__inner {
				overflow: visible;

				@include tablet {
					width: 100%;
				}
				.letter {
					top: 50%;
					bottom: auto;
					transform: translate(0, -50%);
					font-size: 130rem;
					em {
						font-size: 120rem;
						@include full {
							font-size: 120px;
						}
						@include mobile {
							font-size: 74rem;
						}
					}
					@include full {
						font-size: 130px;
					}
					@include mobile {
						top: 50%;
						left: 50%;
						bottom: auto;
						transform: translate(-50%, -50%);
						font-size: 80rem;
					}
					&--01 {
						left: -16rem;
						@include mobile {
							left: 50%;
							top: 122rem;
						}
						@include pc {
							left: -225rem;
						}
					}
					&--02 {
						transform: translate(-50%, -50%);
					}
					&--03 {
						left: auto;
						right: -8rem;
						@include pc {
							right: -230rem;
						}
						@include mobile {
							top: auto;
							left: 50%;
							bottom: 20rem;
							right: auto;
						}
					}
				}
				.note {
					display: block;
					top: 50%;
					bottom: auto;
					transform: translate(0, -50%);
					text-align: center;
					@include mobile {
						top: auto;
						left: 50%;
						transform: translate(-50%, -50%);
						font-size: 14rem;
					}
					@include ie-only {
						top: calc(50% - 15rem);
					}
					&--01 {
						width: 350rem;
						left: calc(50% - 540rem);
						@include pc {
							left: calc(50% - 565rem);
						}
						@include mobile {
							left: 50%;
							top: 32.38%;
						}
					}
					&--02 {
						width: 220rem;
						left: calc(50% + 250rem);
						@include pc {
							left: calc(50% + 275rem);
						}
						@include mobile {
							left: 50%;
							bottom: 32.38%;
						}
					}
				}
			}
		}
	}

	// 키 비주얼 스타일
	.kv {
		position: relative;
		z-index: 1;
		// 캔버스
		&__masking {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			z-index: 8;
			&--off {
				background-color: transparent;
			}
			&--hidden {
				display: none;
			}
		}

		// 백그라운드 이미지
		&__masked {
			position: relative;
			&__keyvisual {
				position: relative;
				width: 100vw;
				height: 100vh;
				background: url('/images/img_main_kv.jpg') no-repeat center / cover;
				transform: scale(0.7);
				@include mobile {
					min-height: 600rem;
				}
				&--scale {
					position: fixed;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background: url('/images/img_main_kv.jpg') no-repeat center / cover;
					opacity: 0;
					&.white-cover {
						background: #fff;
					}
				}
			}
		}
	}

	// 키비 하단 디스크립션 스타일
	.description {
		position: absolute;
		top: 100vh;
		left: 50%;
		z-index: 5;
		width: 100%;
		max-width: 1920px;
		transform: translate(-50%, 0);
		font-size: 46rem;
		font-family: $notosanskrm;
		line-height: 1.43;
		color: #fff;
		@include full {
			font-size: 46px;
		}
		@include mobile {
			font-size: 28rem;
		}
		p {
			margin-left: 108rem;
			@include full {
				margin-left: 108px;
			}
			@include mobile {
				margin-left: #{$side-padding};
			}
		}
	}
}

// Connect 스타일
#connect {
	@include section-set-pc;
	margin: 0 auto;
	padding-top: $connect-top;
	padding-bottom: 250rem;
	z-index: $connect-z-index;
	background: #fff;

	@include mobile {
		@include section-set-mo;
		padding-top: 0;
	}

	.fixed-area {
		// background: rgba(#d2691e, 0.5);
		position: absolute;
		top: 0;
		width: 100vw;
		height: 100vh;
		z-index: 1;
		&--fixed {
			position: fixed;
		}
		&--underfloat {
			top: auto;
			bottom: 0;
		}
		@include mobile {
			padding-top: 0;
		}
	}


	.fraction {
		position: relative;
		width: 100vw;
		height: 100vh;
		max-width: 1920px;

		// 모바일 사용 변수
		$fraction-top: 26.96vh;
		$fraction-bg-top: 80rem;

		@include mobile {
			margin-left: -15rem;
		}
		&__bg {
			position: absolute;
			top: 50%;
			left: 50%;
			width: 739rem;
			height: 457rem;
			transform: translate(-50%, -50%);
			@include mobile {
				top: calc(50% - 10vh);
				width: 340rem;
				height: 285rem;
			}
			&__img {
				position: absolute;
				top: 0;
				left: 0;
				width: 739rem;
				height: 457rem;
				@include full {
					width: 739px;
					height: 457px;
				}
				@include mobile {
					width: 340rem;
					height: 285rem;
					left: 50%;
					transform: translate(-50%, 0);
				}
				background-image: url('/images/img_main_connect-bg_01.png');
				background-repeat: no-repeat;
				background-size: contain;
				background-position: center;
				opacity: 0;
				transition: opacity .5s ease-in;
				&.active {
					opacity: 1;
				}
				&--00 {
					left: -130rem;
					@include mobile {
						left: 50%;
					}
				}
				&--01 {
					background-image: url('/images/img_main_connect-bg_02.png');
				}
				&--02 {
					background-image: url('/images/img_main_connect-bg_02.png');
				}
				&--03 {
					background-image: url('/images/img_main_connect-bg_03.png');
				}
			}
		}
		&__letter {
			position: absolute;
			top: 50%;
			left: 50%;
			width: 100%;
			transform: translate(-50%, -50%);
			z-index: 1;
			@include mobile {
				top: calc(50% - 10vh);
			}
			@include tablet {
				width: 50%;
			}
			li {
				position: relative;
				width: 200%;
				z-index: -1;
				font-size: 210rem;
				font-family: $ambit-semi;
				font-weight: 900;
				line-height: 0.9;
				letter-spacing: -5rem;
				text-transform: uppercase;
				@include mobile {
					font-size: 80rem;
					letter-spacing: 0rem;
					line-height: 1;
				}
				@include full {
					font-size: 210px;
				}
			}
		}
	}

	.section-title {
		position: absolute;
		left: 0;
		bottom: 40rem;
	}

	// 스크롤 사진 스타일
	.pics {
		position: relative;
		z-index: 1;
		width: 50%;
		margin-left: auto;
		padding-top: 150vh;
		padding-bottom: 100vh;
		@include mobile {
			padding-top: 150vh;
		}

		&__list {
			display: block;
			position: static;
			.pic-frame {
				width: 100%;
				height: 100%;
				@include img-effect;
				&__img {
					width: 100%;
					height: 100%;
				}
				> span {
					display: block;
					margin-top: 14rem;
					text-align: right;
					font-family: $ambit-regular;
					font-size: 14rem;
					@include pc {
						margin-top: 14px;
						font-size: 14px;
					}
					@include mobile {
						margin-top: 7rem;
						font-size: 12rem;
					}
				}
			}

			// 사진과 사진 + 갭
			$pic-top-gap: 35vh;

			$pic-width-01-pc: 580;
			$pic-height-01-pc: 580;
			$pic-width-01-mo: 250;
			$pic-height-01-mo: 250;

			$pic-width-02-pc: 922;
			$pic-height-02-pc: 640;

			$pic-width-03-pc: 678;
			$pic-height-03-pc: 450;

			$pic-width-04-pc: 720;
			$pic-height-04-pc: 1020;
			$pic-width-04-mo: 230;
			$pic-height-04-mo: 326;

			&__00 {
				width: #{$pic-width-01-pc}rem;
				height: #{$pic-height-01-pc}rem;
				transform: translate(-50%, 0);
				@include full {
					width: #{$pic-width-01-pc}px;
					height: #{$pic-height-01-pc}px;
				}
				@include mobile {
					width: #{$pic-width-01-mo}rem;
					height: #{$pic-height-01-mo}rem;
				}
			}
			&__01 {
				width: #{$pic-width-02-pc}rem;
				height: #{$pic-height-02-pc}rem;
				margin: #{$pic-top-gap} -20rem 0 auto;
				@include full {
					width: #{$pic-width-02-pc}px;
					height: #{$pic-height-02-pc}px;
					margin: #{$pic-top-gap} -20px 0 auto;
				}
				@include mobile {
					width: 315rem;
					height: 232rem;
					margin: calc(#{$pic-top-gap} + 0rem) auto 0;
				}
			}
			&__02 {
				width: #{$pic-width-03-pc}rem;
				height: #{$pic-height-03-pc}rem;
				margin: calc(194rem + #{$pic-top-gap}) auto 0 40rem;
				vertical-align: middle;
				@include full {
					width: #{$pic-width-03-pc}px;
					height: #{$pic-height-03-pc}px;
					margin: calc(194px + #{$pic-top-gap}) auto 0 40px;
				}
				@include mobile {
					float: none;
					display: block;
					margin: calc(#{$pic-top-gap} + 0rem) 15rem 0 auto;
					width: 272rem;
					height: 180rem;
				}
			}
			&__03 {
				width: #{$pic-width-04-pc}rem;
				height: #{$pic-height-04-pc}rem;
				margin: calc(0rem + #{$pic-top-gap}) 60rem 0 auto;
				@include full {
					width: #{$pic-width-04-pc}rem;
					height: #{$pic-height-04-pc}rem;
					margin: calc(0px + #{$pic-top-gap}) 60px 0 auto;
				}
				@include mobile {
					width: #{$pic-width-04-mo}rem;
					height: #{$pic-height-04-mo}rem;
					margin: 0;
					margin-top: calc(#{$pic-top-gap} + 0rem);
					transform: translate(-22.1875%, 0)
				}
			}
		}
	}
}

//Forest 스타일
#forest {
	@include section-set-pc;

	$forest-padding-top-pc: 0;
	$fore1: 100vw;
	$fore2: 142.76vw;
	$fore3: 100vw;
	$fore4: 138.17vw;
	$fore5: 100vw;

	$forest-section : $fore1, $fore2, $fore3, $fore4, $fore5;
	@function forest-width($widths) {
		$sum: 0;
		@each $width in $widths {
			$sum: $sum + $width;
		}
		@return $sum
	}

	position: relative;
	display:flex;
	width: forest-width($forest-section);
	height: 100vh;
	padding-top: #{$forest-padding-top-pc}rem;
	padding-right: 0;
	padding-left: 0;
	z-index: $forest-z-index;
	flex-wrap:wrap;
	flex-direction:column;
	background-color: #fff;
	transition: background .3s ease-in;

	@include full {
		padding-top: #{$forest-padding-top-pc}px;
		padding-left: 0;
	}

	@include mobile {
		@include section-set-mo;
		padding-top: 200rem;
		display: block;
		width: 100vw;
		height: auto;
	}

	// 포레스트 배경 체인지 스타일
	@at-root .forest-wrap {
		position: relative;
		transition: all .3s ease-in;
		&:before {
			position: absolute;
			content: '';
			top: -100vh;
			left: 0;
			width: 100vw;
			height: 100vh;
			background-color: transparent;
			transition: all .3s ease-in;
			z-index: 40;
			pointer-events: none
		}
		&:after {
			content: '';
			position: absolute;
			top: -1vh;
			left: 0;
			width: 100vw;
			height: 30vh;
			background-color: #fff;
			transition: all .3s ease-in;
			z-index: 41;
			pointer-events: none
		}
		&.black {
			background: #000;
			color: #fff;
			transition: all .3s ease-in;
			&:before {
				background-color: #000;
				transition: all .3s ease-in;
			}
			&:after {
				background-color: #000;
				transition: all .3s ease-in;
			}
			#forest {
				background-color: #000;
				transition: background .3s ease-in;
			}
			.section-title {
				a {
					border-color: #fff;
					background-color: #fff;
					color: #000;
					transition: all .3s ease-in;
				}
			}
		}
	}

	// 이미지 박스 가로 등장 인터렉션
	.img-box {
		@media screen and (min-width: 769px) {
			&:after {
				transition: width 1s .1s cubic-bezier(.19,1,.22,1);
			}
			&.img-hello {
				&:after {
					height: 100%;
					width: 0;
				}
			}
		}
	}

	// 포레스트 가로스크롤 전 첫 페이지 이너
	.forest__inner {
		position: relative;
		width: calc(100vw - 120rem);
		height: calc(100vh - #{$forest-padding-top-pc}rem);
		margin: 0 auto;

		@include full {
			max-width: 1920px;
			width: calc(100vw - 120px);
			height: calc(100vh - #{$forest-padding-top-pc}px);
		}

		@include mobile {
			position: static;
			margin: 0 auto;
			width: 100%;
			height: 100%;
		}
	}


	// 가로스크롤 섹션 키워드, 타이틀 스타일
	.section-title-wrap {
		top: 50%;
		transform: translate(0, -50%);

		@include mobile {
			transform: none;
		}
		@include tablet {
			margin-top: 0;
		}
	}

	// 포레스트 섹션 텍스트 영역
	.text-area {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 100%;
		transform: translate(-50%, -50%);
		color: #fff;
		text-align: center;
		@include mobile {
			position: static;
			transform: none;
		}
		&__title {
			font-size: 56rem;
			line-height: 1.37;
			@include mobile {
				font-size: 32rem;
			}
		}
	}

	// 포레스트 섹션 하단 텍스트 스탬프 영역
	.under-stamp {
		position: absolute;
		bottom: 40rem;
		left: 50%;
		z-index: 1;
		transform: translate(-50%, 0);
		font-size: 46rem;
		font-family: $rosendale;
		color: #fff;
		text-align: center;

		@include full {
			bottom: 34px;
			font-size: 46px;
		}
		@include mobile {
			position: static;
			bottom: 25rem;
			display: block;
			transform: none;
			font-size: 26rem;
		}
	}

	// 포레스트 피쳐 이미지 영역
	.feature-img {
		position: absolute;
		z-index: 1;
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		@include mobile {
			position: relative;
			top: inherit;
			right: inherit;
			bottom: inherit;
			left: inherit;
		}
	}


	// 포레스트 섹션 스타일
	.forest-sec {
		position: relative;
		display: flex;
		width: 100vw;
		height: 100vh;
		@include mobile {
			display: block;
			width: 100%;
			height: auto;
		}

		// 풀페이지 이미지 경로
		$full-page-section-img-1 : '/images/img_main_forest_01.jpg';
		$full-page-section-img-1-mo : '/images/img_main_forest_01-mo.jpg';
		$full-page-section-img-7 : '/images/img_main_forest_07.jpg';
		$full-page-section-img-7-mo : '/images/img_main_forest_07-mo.jpg';
		$section-img-2 : '/images/img_main_forest_02.jpg';
		$section-img-3 : '/images/img_main_forest_03.jpg';
		$section-img-4 : '/images/img_main_forest_04.jpg';
		$section-img-5 : '/images/img_main_forest_05.jpg';
		$section-img-6 : '/images/img_main_forest_06.jpg';

		// 풀페이지 이미지 믹스인
		@mixin full-image-section($img-url) {
			@include mobile {
				margin-top: 30rem;
			}
			.img-area {
				position: absolute;
				top: -#{$forest-padding-top-pc}rem;
				left: 0;
				width: 100%;
				height: 100vh;
				@include mobile {
					height: auto;
				}
				.feature-img {
					width: 100%;
					height: 100vh;
					background-image: url(#{$img-url});
				}
			}
			.text-area {
				bottom: 30rem;
				z-index: 1;
				@include ie-only {
					bottom: -60rem;
				}
				@include mobile {
					margin-bottom: 0;
				}
			}
		}

		@mixin feature-img($top, $right, $bottom, $left, $width, $height, $url) {
			top: $top;
			right: $right;
			bottom: $bottom;
			left: $left;
			width: $width;
			height: $height;
			background-image: url(#{$url});
			@include mobile {
				top: inherit;
				right: inherit;
				bottom: inherit;
				left: inherit;
			}
		}

		// 첫번째 섹션
		&--01 {
			width: $fore1;
			@include mobile {
				position: static;
				width: 100%;
			}
			.img-area {
				margin: 52rem 0 0 -#{$side-padding};
				width: 325rem;
				height: 500rem;
				.feature-img {
					width: 100%;
					height: 100%;
					background: no-repeat center / cover;
					background-image: url(#{$full-page-section-img-1-mo});
				}
			}
			.catchphares {
				position: absolute;
				bottom: 0;
				left: 0;
				@include ie-only {
					bottom: -60rem;
				}
				@include mobile {
					position: static;
					margin-top: 25rem;
					text-align: right;
				}

				&__title {
					@include pc {
						font-size: 190rem;
					}
					@include mobile {
						display: block;
					}
					> .txt-target {
						@include mobile {
							display: block;
						}
					}
					em {
						@include pc {
							font-size: 190rem;
						}
					}
				}
			}
		}

		// 두번째 섹션
		&--02 {
			width: $fore2;
			@include full-image-section($full-page-section-img-1);
			@include mobile {
				display: none;
			}
		}

		// 세번째 섹션
		&--03 {
			// 텍스트 가운데에서 왼쪽으로 기울기
			$left-handed: 8.3333%;

			width: $fore3;
			@include mobile {
				display: none;
			}
			.iamobject {
				position: absolute;
				top: 0;
				right: 0;
				width: auto;
				height: 100%;
				transform: translate(calc(100% - 42.69%), 0);
				z-index: 1;
				@include ie-only {
					right: -25.6770%;
					transform: none;
				}
				img {
					width: auto;
					height: 100%;
					@include mobile {
						display: none;
					}
				}
			}
			.text-area {
				left: calc(50% - #{$left-handed});
				@include mobile {
					position: static;
					margin-bottom: 148rem;
					transform: none;
				}
			}
			.under-stamp {
				left: calc(50% - #{$left-handed});
				font-size: 18rem;
			}
		}

		// 네번째 섹션
		&--04 {
			width: $fore4;
			@include mobile {
				width: 100%;
			}
			.feature-img {
				@include feature-img(auto, auto, 9.0517%, 47.3426%, 608rem, 846rem, $section-img-5);
				transform: translate(6.6049%, 0);
				@include mobile {
					position: static;
					width: 325rem;
					height: 530rem;
					margin: 120rem 0 0 auto;
				}
			}
			.catchphares {
				position: absolute;
				bottom: 0;
				right: 5.5408%;
				z-index: 5;
				@include mobile {
					position: static;
					margin-top: 25rem;
					margin-right: -#{$side-padding};
				}
				@include ie-only {
					bottom: -60rem;
				}
				&__title {
					font-size: 220rem;
					@include mobile {
						display: block;
						font-size: 74rem;
						text-align: right;
					}
					> span {
						@include mobile {
							margin: 0 0 0 auto;
						}
					}
					em {
						font-size: 210rem;
						@include mobile {
							margin: 0 0 0 auto;
							font-size: 74rem;
						}
					}
				}
			}
		}

		// 다섯번째 쌕센
		&--05 {
			width: $fore5; //3240
			@include full-image-section($full-page-section-img-7);
			@include mobile {
				.img-area {
					height: 100%;
					.feature-img {
						height: 100%;
						background-image: url(#{$full-page-section-img-7-mo});
					}
				}

				width: auto;
				margin: 120rem -#{$side-padding} 0;
				height: 590rem;
			}
			.text-area {
				font-family: $notosanskrm;
				top: calc(50% - #{$forest-padding-top-pc/2}rem);
				bottom: auto;
				@include full {
					top: calc(50% - #{$forest-padding-top-pc/2}px);
				}
				@include mobile {
					position: absolute;
					top: 50%;
					width: 100%;
					transform: translate(-50%, -50%);
				}
				&__title {
					font-size: 40rem;
					line-height: 1.5;
					@include mobile {
						font-size: 20rem;
						line-height: 1.5;
					}
				}
			}
			.under-stamp {
				@include mobile {
					position: absolute;
					bottom: 28rem;
					transform: translate(-50%, 0)
				}
			}

		}
	}
}

// Art 스타일
#art {
	@include section-set-pc;
	padding-top: 220rem;
	background: #fff;
	z-index: $art-z-index;


	@include mobile {
		@include section-set-mo;
		padding-top: 160rem;
	}

	$art-list-mo-top-gap: 48rem;

	.art-list {
		display: flex;
		flex-wrap: wrap;
		padding-top: 256rem;
		@include full {
			padding-top: 256px;
		}
		@include mobile {
			padding-top: $art-list-mo-top-gap;
			flex-wrap: nowrap;
			transform: inherit;
		}
	}

	//스와이퍼 디폴트 스타일
	.swiper-wrapper {
		position: relative;
		width: 100%;
		height: 100%;
		z-index: 1;
		display: flex;
		transition-property: transform;
		box-sizing: content-box;
	}

	.swiper-slide {
		flex-shrink: 0;
		width: 100%;
		height: 100%;
		position: relative;
		transition-property: transform;
	}

	.swiper-pagination {
		position: absolute;
		top: 0;
		right: 0;
		text-align: center;
		transition: 300ms opacity;
		transform: translate3d(0, 0, 0);
		z-index: 10;
	}
	//스와이퍼 디폴트 스타일


	.swiper-container {
		@include mobile {
			position: relative;
		}
		.swiper-wrapper {
			.swiper-slide {
				width: calc(33.33% - 13.33rem);
				margin-left: 20rem;
				@include mobile {
					width: 235rem;
					height: auto;
					margin-left: inherit;
				}
				.img-boxes {
					font-size: 18rem;
					@include full {
						font-size: 18px;
					}
					@include img-effect;
					p {
						display: flex;
						margin-top: 14rem;
						font-family: $ambit-regular;
						@include full {
							margin-top: 14px;
						}
						@include mobile {
							font-size: 14rem;
						}
						> em {
							width: 50%;
							font-size: 18rem;
							font-family: $rosendale;
							@include full {
								font-size: 18px;
							}
							@include mobile {
								font-size: 14rem;
							}
						}
						> span {
							width: 50%;
							display: block;
							text-transform: uppercase;
							> strong {
								font-size: 18rem;
								display: block;
								& + span {
									margin-top: 18rem;
									@include full {
										margin-top: 18px;
									}
								}
								@include full {
									font-size: 18px;
								}
								@include mobile {
									font-size: 14rem;
								}
							}
							> span {
								display: block;
								font-size: 12rem;
								font-family: $ambit-regular;
								line-height: 1.2;
								@include full {
									font-size: 12px;
								}
								@include mobile {
									font-size: 11rem;
								}
							}
						}
					}
				}

				$first-image-top-gap: 363;
				$second-image-top-gap: 180;
				$third-image-top-gap: 0;

				$first-image-top-gap-mo: 0;
				$second-image-top-gap-mo: 115;
				$third-image-top-gap-mo: 0;


				&:nth-child(1),
				&:nth-child(4) {
					margin-left: 0;
					@include mobile {
						margin-left: 0;
					}
				}
				&:nth-child(n+4) {
					margin-top: 140rem;
					@include mobile {
						margin-top: 25rem;
					}
				}

				&:nth-child(1) {
					margin-top: #{$first-image-top-gap}rem;
					@include full {
						margin-top: #{$first-image-top-gap}px;
					}
					@include mobile {
						margin-top: #{$first-image-top-gap-mo}rem;
						// margin-left: 58rem;
						margin-left: 18.0967%;
					}
				}
				&:nth-child(2) {
					margin-top: #{$second-image-top-gap}rem;
					@include full {
						margin-top: #{$second-image-top-gap}px;
					}
					@include mobile {
						margin-top: #{$second-image-top-gap-mo}rem;
					}
				}
				&:nth-child(3) {
					@include mobile {
						margin-top: #{$third-image-top-gap-mo}rem;
					}
				}
			}
		}
		.swiper-pagination {
			display: none;
			font-size: 14rem;
			font-family: $ambit-regular;
			@include mobile {
				display: block;
				top: $art-list-mo-top-gap;
				transform: translate(0,calc(-100% - 17rem))
			}
			&-current {
				position: relative;
				left: 3rem;
			}
			&-total {
				position: relative;
				left: -3rem;
			}
		}
	}
}

// Safety 스타일
#safety {
	@include section-set-pc;
	padding-top: 240rem;
	@include full {
		padding-top: 240px;
	}
	z-index: $safety-z-index;
	background-color: #fff;

	$safety-content-width: 1180;
	$safety-content-margin: 60;


	@include mobile {
		@include section-set-mo;
		padding-top: 180rem;

		.section-title {
			div.motion-txt {padding-bottom: 2px;}
		}
	}

	.safety {
		display: flex;
		margin-top: 0;

		@include mobile {
			display: block;
		}

		&__img {
			width: 50%;
			margin: 0 -#{$safety-content-margin}rem 0 auto;
			@include mobile {
				width: calc(100% + #{$side-padding * 2});
				margin: 39rem 0 0 -#{$side-padding};
			}
			img {
				width: 100%;
			}
		}
	}

	.words {
		position: absolute;
		bottom: 0;
		left: 0;
		display: flex;
		width: 50%;
		margin: 57rem 0 0 auto;
		justify-content: space-between;

		@include mobile {
			position: static;
			display: block;
			margin-top: 35rem;
			width: 100%;
			flex-wrap: wrap;
		}
		> li {
			width: 440rem;
			@include mobile {
				width: 100%;
				& + li {
					margin-top: 25rem;
					border-top: 1px solid #000;
					padding-top: 27rem;
				}
			}
			h3 {
				font-size: 26rem;
				line-height: 1.1;
				text-transform: uppercase;
				font-family: $ambit-semi;
				@include mobile {
					font-size: 18rem;
				}
			}
			p {
				margin-top: 24rem;
				font-size: 24rem;
				line-height: 1.62;
				font-family: $notosanskr;
				@include mobile {
					margin-top: 16rem;
					font-size: 16rem;
				}
			}
		}
	}
}

// explore now
#exp {
	position: relative;
	z-index: $exp-z-index;
	padding: 420rem 0 108rem 0;
	background: #fff;
	@include full {
		padding: 420px 0 108px 0;
	}
	@include mobile {
		padding-top: 146rem;
	}

	.exp-inner {
		position: relative;
		figure {
			display: block;
			width: 450rem;
			height: 620rem;
			margin: 0 auto;
			@include full {
				width: 450px;
				height: 620px;
			}
			@include mobile {
				width: 210rem;
				height: 295rem;
			}
			img {
				width: 100%;
				height: 100%;
			}
		}
		.link {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			@include mobile {
				width: 100%;
				text-align: center;
			}
			a {
				position: relative;
				display: inline-block;
				font-size: 120rem;
				font-family: $ambit-semi;

				@include full {
					font-size: 120px;
				}
				@include mobile {
					font-size: 46rem;
				}
				&:before {
					content: '';
					position: absolute;
					bottom: 4px;
					left: 0;
					width: 100%;
					height: 5px;
					background-color: #000;
					@include mobile {
						height: 2px;
					}
				}
				em {
					font-family: $rosendale;
					font-size: 112rem;
					@include full {
						font-size: 112px;
					}
					@include mobile {
						font-size: 46rem;
					}
				}
				span {
					text-transform: uppercase;
				}
			}
		}
	}
}
